.App {
  text-align: center;
  width: 40%;
  max-width: 1080px;
  margin: 0 auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.nav {
  height: 60px;
  width: 100%;
  display: flex;
  background-color: #ccc;
  color: #fff;
  padding: 20px;
  font-weight: 700;
}

div {
  box-sizing: border-box;
}

.list {
  text-align: left;
  width: 100%;
  overflow: hidden;
  padding-left: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid grey;
}




.modal {
  width: 100%;
  height: 100%;
  margin-top: 20px;
  padding: 20px;
  background-color: #eee;
  text-align: left;
}





.inputTitle {
  margin-top: 10px;
  margin-left: 20px;
}

.inputDetails{
  margin-top: 10px;
  margin-left: 20px;
}

.postBtn{
  margin-top: 10px;
  margin-left: 20px;
  margin-bottom: 10px;
}

.edit{
  width: 100%;
  background-color: #eee;
  border-top: 1px solid grey;
}

